@charset "utf-8";

@import "../vendor/bootstrap-sass/assets/stylesheets/bootstrap";
@import "../vendor/bootstrap-material-design/sass/bootstrap-material-design";
@import "../vendor/bootstrap-material-design/sass/ripples";

/* =variables */
$base-font-family: "Nanum Gothic", Roboto, Helvetica, Arial, sans-serif;
$base-font-family: "Apple SD Gothic Neo", "Nanum Gothic", "", "Malgun Gothic";
$base-font-size:   16px;
$base-font-weight: 300;
$small-font-size:  $base-font-size * 0.8;
$base-line-height: 2em;

$spacing-unit:     30px;

$text-color:       #555555;
$background-color: #FAFAFA;
$brand-color:      #00BFF3;
$selection-color:  $brand-color;

$navbar-default-bg:#222629;

$btn-default-bg:   #25292c;
$btn-primary-bg:   $brand-color;

$grey-color:       #E1E1E1;
$grey-color-light: #CDCfD7;
$grey-color-dark:  #A7A9A9;
$grey-very-light:  #F5F5F5;

$content-width:    800px;
$on-palm:          600px;
$on-laptop:        800px;

/* =global */
body {
  color:       $text-color;
  line-height: $base-line-height;
  background:  $background-color;
  -webkit-font-smoothing: subpixel-antialiased;
}

//::-webkit-scrollbar {
//  width: 0 !important;
//}

::selection {
  background: $selection-color;
  color: #FFF;
  text-shadow: none;
}

::-moz-selection {
  background: $selection-color;
  color: #FFF;
  text-shadow: none;
}

// Force override bootstrap by adding id (100 points)
#app {
  font-family: $base-font-family;

  /* =heading */
  h1, h2, h3, h4, h5, h6 {
    text-space: -1px;
    font-weight: $base-font-weight;
  }

  /* =anchor */
  a {
    color: $brand-color;
    text-decoration: none;

    &:hover {
      color: darken($brand-color, 10%);
    }
  }

  /* =blockquote */
  blockquote {
    border: none;
    border-radius: 0;
    margin: 0 -30px 20px;
    padding: 30px;

    small, footer, cite {
      color: #FFF;
      font-size: $small-font-size;
    }

    footer {
      padding-left: 50px;
      margin-bottom: 10px;
    }

    cite {
      text-decoration: underline;
    }
  }

  blockquote, blockquote p {
    position: relative;
    font-size: $base-font-size * 1.4;
    font-style: italic;
    padding: $base-font-size $base-font-size $base-font-size 50px;
    line-height: 1.5em;
    color: #FFF;
    background-color: #6739b6;
  }

  blockquote p:first-child {
    &:before {
      content: url(../images/icon-quote.png) no-close-quote;
      position: absolute;
      top: $base-font-size;
      left: 0;
    }
  }

  blockquote p:nth-of-type(2) {
    display: none;
  }

  .badge {
    background-color: $grey-color;
    font-weight: normal;
  }

  #main .box-body {
    font-size: $base-font-size;

    h1, h2, h3, h4, h5, h6 {
      font-weight: bold;
    }

    p, :not(blockquote p) {
      line-height: $base-line-height;
      margin: 0 0 2em;
    }
  }

  /* =image */
  #main img {
    width: 100%;
    max-width: 400px;
    display: block;
    margin: 16px auto;
    padding: 4px;
    border: 1px solid $grey-color-light;
    border-radius: 4px;
    transition: all .2s ease-in-out;
    height: auto;

    a {
      border: none;
    }
  }

  #main.minimal img {
    border: none;
    margin: 3em auto;
    max-width: 600px;
  }

  .material-icons {
    vertical-align: middle;
  }

  /* =nav */
  .navbar {
    background-color: $navbar-default-bg;
    color: #FFF;
    margin-bottom: 40px;

    a, a:hover, a:focus {
      color: #FFF;
    }

    .navbar-nav {
      li {
        min-width: 80px;
      }

      &>.active>a, a:hover, a:focus {
        border-bottom: 2px solid $brand-color;
        transition: all .15s ease-in-out;
      }
    }
  }

  /* =button */
  .btn {
    height: 38px;
    padding-right: 20px;
    border-radius: 19px;
    color: #FFF;
    text-transform: none;
    font-size: 1em;
    font-style: normal;
    font-weight: 300;

    &:hover, &:focus {
      opacity: 0.9;
      color: #FFF;
      transition: all .2s ease-in-out;
    }
  }

  .btn-default {
    background-color: $btn-default-bg;
  }

  .btn-primary {
    background-color: $btn-primary-bg;
  }

  /* =box */
  .box {
    margin: 0 0 30px 0;
    padding: 30px;
    box-shadow: none;
    border-radius: 0;
    background-color: #FFF;

    .highlighter-rouge {
      margin-top: 1em;
      margin-bottom: 2em;

      pre {
        border: none;
        border-radius: 0;
        margin: 0 -30px;
        padding: 30px;
        color: #d4d7d6;
        background-color: $navbar-default-bg;
      }

      // Seti theme
      // @see https://gist.github.com/sheck/a4d55cd7142d016e2a5c
      .c { color: #888888;} /* Comment */
      .err { color: #d4d7d6;} /* Error */
      .esc { color: #d4d7d6;} /* Escape */
      .g { color: #d4d7d6;} /* Generic */
      .k { color: #9fca56;} /* Keyword */
      .l { color: #d4d7d6;} /* Literal */
      .n { color: #d4d7d6;} /* Name */
      .o { color: #d4d7d6;} /* Operator */
      .x { color: #d4d7d6;} /* Other */
      .p { color: #d4d7d6;} /* Punctuation */
      .cm { color: #41535b;} /* Comment.Multiline */
      .cp { color: #ff026a;} /* Comment.Preproc */
      .c1 { color: #888888;} /* Comment.Single */
      .cs { color: #41535b;} /* Comment.Special */
      .gd { color: #870505;} /* Generic.Deleted */
      .ge { color: #d4d7d6; text-decoration: underline;} /* Generic.Emph */
      .gr { color: #d4d7d6;} /* Generic.Error */
      .gh { color: #d4d7d6; font-weight: bold;} /* Generic.Heading */
      .gi { color: #d4d7d6; font-weight: bold; background-color: #43800a } /* Generic.Inserted */
      .go { color: #2b2e28; background-color: #1f2122 } /* Generic.Output */
      .gp { color: #d4d7d6;} /* Generic.Prompt */
      .gs { color: #d4d7d6;} /* Generic.Strong */
      .gu { color: #d4d7d6; font-weight: bold;} /* Generic.Subheading */
      .gt { color: #f8f8f8;} /* Generic.Traceback */
      .kc { color: #9fca56;} /* Keyword.Constant */
      .kd { color: #9fca56;} /* Keyword.Declaration */
      .kn { color: #9fca56;} /* Keyword.Namespace */
      .kp { color: #9fca56;} /* Keyword.Pseudo */
      .kr { color: #9fca56;} /* Keyword.Reserved */
      .kt { color: #55b5db;} /* Keyword.Type */
      .ld { color: #d4d7d6;} /* Literal.Date */
      .m { color: #cd3f45;} /* Literal.Number */
      .s { color: #55b5db;} /* Literal.String */
      .na { color: #55b5db;} /* Name.Attribute */
      .nb { color: #d4d7d6;} /* Name.Builtin */
      .nc { color: #d4d7d6;} /* Name.Class */
      .no { color: #cd3f45;} /* Name.Constant */
      .nd { color: #d4d7d6;} /* Name.Decorator */
      .ni { color: #d4d7d6;} /* Name.Entity */
      .ne { color: #d4d7d6;} /* Name.Exception */
      .nf { color: #55b5db;} /* Name.Function */
      .nl { color: #55b5db;} /* Name.Label */
      .nn { color: #d4d7d6;} /* Name.Namespace */
      .nx { color: #d4d7d6;} /* Name.Other */
      .py { color: #d4d7d6;} /* Name.Property */
      .nt { color: #9fca56;} /* Name.Tag */
      .nv { color: #e6cd69;} /* Name.Variable */
      .ow { color: #9fca56;} /* Operator.Word */
      .w { color: #d4d7d6;} /* Text.Whitespace */
      .mb { color: #cd3f45;} /* Literal.Number.Bin */
      .mf { color: #cd3f45;} /* Literal.Number.Float */
      .mh { color: #cd3f45;} /* Literal.Number.Hex */
      .mi { color: #cd3f45;} /* Literal.Number.Integer */
      .mo { color: #cd3f45;} /* Literal.Number.Oct */
      .sb { color: #55b5db;} /* Literal.String.Backtick */
      .sc { color: #55b5db;} /* Literal.String.Char */
      .sd { color: #55b5db;} /* Literal.String.Doc */
      .s2 { color: #55b5db;} /* Literal.String.Double */
      .se { color: #55b5db;} /* Literal.String.Escape */
      .sh { color: #55b5db;} /* Literal.String.Heredoc */
      .si { color: #55b5db;} /* Literal.String.Interpol */
      .sx { color: #55b5db;} /* Literal.String.Other */
      .sr { color: #55b5db;} /* Literal.String.Regex */
      .s1 { color: #55b5db;} /* Literal.String.Single */
      .ss { color: #55b5db;} /* Literal.String.Symbol */
      .bp { color: #d4d7d6;} /* Name.Builtin.Pseudo */
      .vc { color: #e6cd69;} /* Name.Variable.Class */
      .vg { color: #e6cd69;} /* Name.Variable.Global */
      .vi { color: #e6cd69;} /* Name.Variable.Instance */
      .il { color: #cd3f45;} /* Literal.Number.Integer.Long */
    }

    .box-header, .box-body, .box-footer {
      margin: 0;
      padding: 0;
      height: auto;
    }

    .box-header {
      h1, h2, h3, h4 {
        margin-top: 10px;
        margin-bottom: 10px;
      }

      .dropdown-menu {
        border-radius: 0;
        box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);

        &>li>a {
          font-weight: 300;
        }
      }
    }

    .box-meta a {
      color: $grey-color-light;

      &:hover, &:focus {
        color: $grey-color-dark;
      }
    }

    .action a {
      color: $text-color;

      &:hover, &:focus {
        color: $text-color;
      }
    }

    .box-meta {
      color: $grey-color-light;
      margin-bottom: 20px;

      .material-icons {
        font-size: $small-font-size;
        line-height: $small-font-size;
        margin-right: 5px;
      }

      span {
        margin-right: 20px;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    .box-body {
      h1, h2, h3, h4, h5, h6 {
        margin-top: 2em;
        margin-bottom: 1em;

        &:hover .header-link {
          opacity: 1;
          cursor: pointer;
        }
      }

      h1 {font-size: (36 * 0.95) + px;}
      h2 {font-size: (30 * 0.95) + px;}
      h3 {font-size: (24 * 0.95) + px;}
      h4 {font-size: (18 * 0.95) + px;}

      p, :not(blockquote p) {
        margin: 0 0 1.2em;
      }

      .header-link {
        position: relative;
        left: 0.5em;
        opacity: 0;
        font-size: 0.8em;
        transition: opacity 0.2s ease-in-out 0.1s;
      }

      #gravatar {
        @extend .img-circle;
        width: 160px;
        height: 160px;
        border-radius: 50%;
      }

      /* =spacer */
      .spacer {
        text-align: center;
        margin: ($base-font-size * 3) auto;
      }
    }

    .box-tags {
      height: 60px;
      line-height: 60px;
      margin: 30px -30px -30px;
      padding: 0 30px;
      background-color: $grey-very-light;

      span {
        color: $grey-color-light;
      }

      a {
        color: $grey-color-dark;

        &:hover, &:focus {
          color: $brand-color;
        }
      }
    }
  }

  /* =sidebar */
  #sidebar  {
    padding: 0;

    .box {
      padding: 0;
    }

    .box-header {
      position: relative;
      padding: 24px;
      background-color: $grey-very-light;

      &:before {
        content: url(../images/arrow.png);
        position: absolute;
        bottom: -10px;
        left: 24px;
      }
    }

    .box-body {
      padding: 0;

      ul {
        list-style: none;
        margin-left: 0;
        padding-left: 0;
      }

      li, div.tags, form {
        padding: 1em 24px;
      }

      li {
        border-bottom: 1px solid $grey-very-light;

        &:last-child {
          border-bottom: none;
        }

        &.active {
          color: $brand-color;
        }
      }

      a {
        color: $text-color;

        &:hover, &:focus {
          color: $brand-color;
        }
      }

      #q-results li.active {
        background-color: $grey-very-light;
      }

      li>a {
        display: block;

        &:after {
          font-family: "Material Icons";
          content: "\E409";
          position: absolute;
          right: 24px;
        }
      }

      h1, h2, h3, h4 {
        font-size: $base-font-size;
        font-weight: 300;

        small, a {
          display: block;
        }

        small {
          margin-bottom: $base-font-size;
        }
      }

      .tags {
        word-bread: break-word;
      }

      .dsq-widget-avatar {
        display: inline-block;
        margin-right: 10px;
        padding: 2px;
        border: 1px solid $grey-color-light;
        border-radius: 4px;
        transition: all .2s ease-in-out;

        a {
          border: none;
        }
      }

      .dsq-widget-list li>a {
        line-height: 32px;

        &:after {
          content: "";
        }
      }

      .dsq-widget-user {
        margin-bottom: 10px;
      }

      .dsq-widget-meta {
        display: inline-block;
        font-size: $small-font-size;

        a:first-child {
          display: none;
        }

        a:last-child {
          &:after {
            font-family: "Material Icons";
            content: "\E409";
            position: absolute;
            right: 24px;
          }
        }
      }

      .facebook-meta {
        font-size: $small-font-size;

        a:after {
          font-family: "Material Icons";
          content: "\E409";
          position: absolute;
          right: 24px;
        }
      }
    }
  }

  /* =pagination */
  #pagination {
    .pager {
      background-color: #FFF;
      margin: 30px auto;

      li a {
        background-color: $brand-color;
        padding: 12px 16px;
        border-radius: 0;
        border: none;
      }

      li.disabled a {
        background-color: $grey-color-light;
        color: $grey-color-dark;
      }

      a, a:hover, a:focus {
        color: #FFF;
      }

      .pager-title-sm {
        display: none;
      }
    }
  }

  /* =site-footer */
  #site-footer {
    background-color: $navbar-default-bg;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;

    h4 {
      font-weight: 300;
    }

    a, a:hover, a:focus {
      color: #FFF;
    }

    p {
      font-size: $small-font-size;
    }
  }

  #back-to-top {
    z-index: 999;
    position: fixed;
    bottom: 16px;
    right: 16px;
    padding: 10px;
    background-color: rgba(0, 191, 243, 0.8);
    a, a:hover, a:focus {
      color: #FFF;
    }
  }

  .tags {
    .box-header {
      margin-bottom: 20px;
    }

    ul {
      padding-left: 0;
      list-style: none;
    }

    h4 {
      position: relative;
      padding: 5px 16px;

      small, &>a {
        display: block;
        margin: 10px 0;
      }

      &>a:after {
        font-family: "Material Icons";
        content: "\E409";
        position: absolute;
        right: 40px;
      }

      &:hover {
        background-color: $grey-very-light;
        color: $grey-color-dark;
      }
    }

    .tags-cloud {
      padding: 5px 16px;
      line-height: 2.4em;

      li {
        display: inline-block;
      }

      a {
        background: $grey-very-light;
        padding: 5px 7px;
        color: $text-color;
        margin-right: 5px;

        .badge {
          margin-right: 7px;
        }
      }

      a:hover, a:focus, li .active {
        background-color: $brand-color;
        color: #FFF;
      }
    }
  }

  /* =light-box */
  .ekko-lightbox {
    a:hover {
      text-decoration: none;
      opacity: 1;
    }

    .left {
      left: 0;
      float: left;
      padding-left: 15px;
      text-align: left;
    }

    .right {
      right: 0;
      float: right;
      padding-right: 15px;
      text-align: right;
    }

    .modal-footer {
      text-align: left;
    }
  }

  .ekko-lightbox-container {
    position: relative;
  }

  .ekko-lightbox-nav-overlay {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;

    a {
      z-index: 100;
      display: block;
      width: 49%;
      height: 100%;
      font-size: 30px;
      color: #fff;
      text-shadow: 2px 2px 4px #000;
      opacity: 0;
      filter: dropshadow(color=#000000, offx=2, offy=2);
      -webkit-transition: opacity 0.5s;
      -moz-transition: opacity 0.5s;
      -o-transition: opacity 0.5s;
      transition: opacity 0.5s;
    }

    a:empty {
      width: 49%;
    }
  }
}

/* Tablet and under-size devices */
@media (max-width: $screen-md-min) {
  #app {
    .box .box-body {
      h1 {font-size: (36 * 0.85) + px;}
      h2 {font-size: (30 * 0.85) + px;}
      h3 {font-size: (24 * 0.85) + px;}
      h4 {font-size: (18 * 0.85) + px;}
    }

    //blockquote, blockquote p {
    //  padding: $base-font-size $base-font-size $base-font-size 50px;
    //}

    blockquote, blockquote p {
      font-size: $base-font-size * 1.2;
      font-style: italic;
      padding: $base-font-size;
      line-height: 1.4em;
    }

    blockquote p:first-child:before {
      content: "";
    }

    blockquote {
      footer {
        padding-left: 20px;
      }
    }
  }
}

/* Tablet and under-size devices */
@media (max-width: $screen-sm-min) {
  #app {
    blockquote, blockquote p {
      font-size: $base-font-size * 1.1;
      font-style: italic;
      line-height: 1.3em;
    }

    blockquote {
      footer {
        padding-left: 16px;
      }
    }

    .container, .col-md-9, .col-md-3 {
      padding-left: 5px;
      padding-right: 5px;
    }

    .navbar .navbar-nav a:focus, .navbar .navbar-nav a:hover, .navbar .navbar-nav > .active > a {
      border: none;
    }

    .box .box-body {
      h1 {font-size: (36 * 0.8) + px;}
      h2 {font-size: (30 * 0.8) + px;}
      h3 {font-size: (24 * 0.8) + px;}
      h4 {font-size: (18 * 0.8) + px;}
    }

    #main {
      .box-meta {
        span:last-child,
        span:nth-child(2) {
          display: none;
        }
      }
    }

    #sidebar .box-body {
      ul li {
        margin-bottom: 0;

        a, h4, small, i {
          margin-bottom: 0;
        }
      }

      .box-meta {
        margin-bottom: 10px;
      }
    }

    #pagination {
      .pager {
        li a {
          padding: 12px 10px;
        }

        .pager-title {
          display: none;
        }

        .pager-title-sm {
          display: inline-block;
        }
      }

      li, a, span, i {
        margin-bottom: 0;
      }
    }

    #site-footer {
      padding-top: 4px;
      padding-bottom: 4px;

      h4 {
        font-size: 16px;
      }

      p {
        font-size: $small-font-size * 0.8;
      }
    }
  }
}

@media print {
  .navbar, #sidebar, #pagination, .comments, #site-footer, .box-meta span:last-child, #back-to-top { display: none; }
}
